<template>
    <div class="tmpl">
      <nav-bar title="用户资料"></nav-bar>
      <div class="mui-page-content">
        <div class="mui-scroll-wrapper">
          <div class="mui-scroll">
            <ul class="mui-table-view">
              <li class="mui-table-view-cell">
                <input type="file" id="file" @change="uploadImg()" hidden >
                <a id="head" class="mui-navigate-right" style="line-height: 40px;">
                  头像
                  <span class="mui-pull-right head" @click="clickFile()">
									  <img class="head-img mui-action-preview" id="head-img" :src="face"/>
								  </span>
                </a>
              </li>
              <li class="mui-table-view-cell">
                <a>用户名<span class="mui-pull-right">{{userInfo.name}}</span></a>
              </li>
            </ul>
            <ul class="mui-table-view">
              <li class="mui-table-view-cell">
                <a>手机号<span class="mui-pull-right">{{userInfo.phone}}</span></a>
              </li>
              <li class="mui-table-view-cell">
                <a>邮箱地址<span class="mui-pull-right">{{userInfo.email}}</span></a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  import Cookie from '../common/cookie.js'
    export default {
      name: "UserInfo",
      data(){
        return{
          userInfo:{},//用户资料
          face:Cookie.getCookie("face"),
        }
      },
      created(){
        let username = this.$route.query.username;
        //console.log(username);
        //获取用户信息
        this.$axios.get(this.$host+"/api/getUserInfo/"+username,{
          headers:{
            "token":Cookie.getCookie("token"),
          },
        })
          .then(res=>{
            if(res.data.code == 200)
            {
              this.userInfo = res.data.data;
            }else{
              Toast(res.data.msg);
              this.$router.go(-1);
            }
          })
      },
      methods:{
        clickFile(){
          document.getElementById("file").click();
        },
        uploadImg(){
          let file = document.getElementById("file").files[0];
          let ext = file.name.split(".")[1];
          let size = file.size;
          if(ext != "jpg" && ext != "jpeg" && ext != "png"){
            Toast("上传失败，头像格式只能是jpg/jpeg/png");
            return;
          }
          if(size > 2*1024*1024){
            Toast("上传失败，头像大小不能大于2M");
            return;
          }
          let image = new FormData();
          image.append("face",file);
          image.append("username",Cookie.getCookie("username"))
          this.$axios.post(this.$host+"/api/updateFace/",image,{
            headers:{
              "Content-Type": "multipart/form-data",
              "token":Cookie.getCookie("token")
            }
          })
          .then(res=>{
            if(res.data.code == 200)
            {
              Toast(res.data.msg);
              this.face = res.data.data.face;
              //更新Cookie
              Cookie.setCookie("face",this.face,7);
            }else{
              Toast(res.data.msg);
            }
            })
        }
      },
    }
</script>

<style scoped>
  .mui-page-content {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: #efeff4;
  }
  .mui-scroll-wrapper,
  .mui-scroll {
    position: static;
    background-color: #efeff4;
  }

  .mui-page .mui-table-view:first-child {
    margin-top: 15px;
  }
  .mui-page .mui-table-view:last-child {
    margin-bottom: 30px;
  }
  .mui-table-view {
    margin-top: 20px;
  }

  .mui-table-view span{
    color: #999;
  }
  .head{
    line-height: 40px;
  }
  .head-img {
    width: 40px;
    height: 40px;
  }
</style>



